.products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    /* border: 1px solid red; */
}

.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 350px;
    /* background-color: yellow; */
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-bottom: 1rem;
    margin-right: 1rem;
    text-align: center;
    padding: 1rem;
}

.product > * {
    /* border: 1px solid red; */
}

.img-container {
    /* border: 5px solid blue; */
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image {
    /* max-width: 70%;
    max-height: 100%; */
    max-width: 150px;
    max-height: 150px;
    object-fit: contain;

}

.price {
    /* border: 1px solid red; */
    width: 100%;
    text-align: left;
}

.btn-addToCart {
    width: 100%;
}
