@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #f9f9f9;
}
  
.navbar-container {
    display: flex;
    background-color: #000000;
    justify-content: space-around;
    align-items: center;
    height: 15vh;
}


.navbar-title {
    /* width: 50%; */
    /* font-size: 1.5rem; */
    font-size: 2.5rem;
    font-weight: 700;
    color: #68e1fd;
}


ul {
    display: flex;
    list-style: none;
}


li {
    transition: transform 0.5s;
}

li:hover {
    transform: translateY(-3px);
}

.link {
    margin-right: 5rem;
    font-size: 1.5rem;
    font-weight: 500;
    text-decoration: none;
    color: #f9f9f9;
    /* transition: transform 0.3s; */
}

.link:hover {
    color: #68e1fd;
    /* transform: translateY(-10px); */
  
}



@media screen and (max-width: 600px) {

    .navbar-container {
        display: flex;
        flex-direction: column;
        background-color: #000000;
        /* justify-content: space-around;
        align-items: center; */
        height: 25vh;
    }
    
    
    .navbar-title {
        font-size: 2.5rem;
        font-weight: 700;
    }
    
    
    ul {
        display: flex;
        list-style: none;
        justify-content: center;
    }
    
    
    
    .link {
        margin-right: 1.5rem;
    }
    
  }






