.cart-container {
    border: 1px solid blue;
}

.cart-item {
    padding: 1rem;
    margin: 1rem;
    border: 1px solid red;
    display: flex;
}

.cart-item-image-container {
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart-item-image {
    max-width: 200px;
    max-height: 200px;
    object-fit: contain;
    
}

.cart-item-desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: rebeccapurple;
}

.cart-item-button {
    display: flex;
}

.cart-item-price {
    text-align: center;
}